@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap');

:root {
  --quiz-status: block;
  --quiz-status-show: none;

  --question1-display: block;
  --question2-display: block;
  --question3-display: block;
  --height-vh: 100%;
}

.publicContainer {
  position: static;
  width: 100%;
  display: block;
  background-color: #fff8e8;
  overflow: hidden;
  height: var(--height-vh);
}

.publicContainer .presentation {
  display: var(--quiz-status);
}

.publicContainer img {
  width: 100%;
}

.publicContainer .quiz {
  background-color: #fff8e8;
  display: var(--quiz-status-show);
}

.publicContainer .quiz .welcome {
  z-index: 3;
  padding-top: 35vh;
  padding-bottom: 35vh ;
}

.publicContainer .quiz .welcome h1 {
  z-index: 4;
  font-size: 100px;
  font-family: Abril Fatface;
  color: #af6c41;
  display: table;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  padding: 20px;
  padding-left: 5%;
  padding-right: 5%;
  border-radius: 20px;
  background: #f1d1ab;
  box-shadow: 0 1rem 2rem rgba(25, 25, 29, 0.18);
}

.publicContainer .quiz .welcome .input {
  z-index: -1;
  display: flex;
  gap: 1vw;
}

.publicContainer .quiz .welcome input {
  z-index: 1;
  display: var(--margin-transition-view);
  margin-left: auto;
  left: 0;
  width: 20%;
  right: 0;
  padding: 20px;
  border-radius: 20px;
  box-shadow: 0 1rem 2rem rgba(25, 25, 29, 0.18);
  transition: all 300ms ease;
  margin-top: 5vh;
}

.publicContainer .quiz .welcome input:hover {
  z-index: 1;
  box-shadow: none;
}

.publicContainer .quiz .welcome h3 {
  z-index: 1;
  color: #af6c41;
  background-color: white ;
  display: var(--margin-transition-view);
  margin-right: auto;
  left: 0;
  right: 0;
  padding: 20px;
  border-radius: 20px;
  margin-top: 5vh;
  box-shadow: none;
  transition: all 300ms ease;
}

.quiz #wait {
  box-shadow: 0 1rem 2rem rgba(25, 25, 29, 0.18);
  position: absolute;
  text-align: center;
  margin-left: 40vw;
  margin-right: 40vw;
}

.publicContainer .quiz .welcome h3:hover {
  z-index: 1;
  box-shadow: 0 1rem 2rem rgba(25, 25, 29, 0.18);
  background-color: #fff8e8;
}

.fadeOut{
  opacity:0;
  transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
}
.fadeIn{
  opacity:1;
  transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
}

.fadeAllOut{
  opacity:0;
  transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
}

.publicContainer .quiz .welcome .awnsers {
  text-align: center;
  column-count: 2;
  column-gap: 10px;
  row-gap: 20px;
  width: 50vw;
  margin-left: auto;
  margin-right: auto;
  font-size: 20px;
}

.publicContainer .quiz .welcome #Question1{
  display: var(--question1-display);
}

.publicContainer .quiz .welcome #Question2{
  display: var(--question2-display);
}

.publicContainer .quiz .welcome #Question3{
  display: var(--question3-display);
}

.publicContainer .quiz .welcome .awnsers #awnser1{
  box-shadow: 0 1rem 2rem rgba(25, 25, 29, 0.18);
  padding: 10px;
  border-radius: 10px;
  background-color: #f19393;
}
.publicContainer .quiz .welcome .awnsers #awnser2{
  box-shadow: 0 1rem 2rem rgba(25, 25, 29, 0.18);
  padding: 10px;
  border-radius: 10px;
  background-color: #b1f193;
  margin-top: 10px;
  margin-bottom: 90px;
}
.publicContainer .quiz .welcome .awnsers #awnser3{
  box-shadow: 0 1rem 2rem rgba(25, 25, 29, 0.18);
  padding: 10px;
  border-radius: 10px;
  background-color: #93def1;
}
.publicContainer .quiz .welcome .awnsers #awnser4{
  box-shadow: 0 1rem 2rem rgba(25, 25, 29, 0.18);
  padding: 10px;
  border-radius: 10px;
  background-color: #ee93f1;
  margin-top: 10px;
}

@media screen and (max-width: 768px) {
  .publicContainer .quiz .welcome h1 {
    z-index: 4;
    font-size: 50px;
    font-family: Abril Fatface;
    color: #af6c41;
    display: table;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    padding: 20px;
    padding-left: 5%;
    padding-right: 5%;
    border-radius: 20px;
    background: #f1d1ab;
    box-shadow: 0 1rem 2rem rgba(25, 25, 29, 0.18);
  }

  .publicContainer .quiz .welcome .awnsers {
    font-size: 13px;
  }
}