@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700,800,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap);
* {
    box-sizing:content-box;
    box-sizing:initial;
}

.container {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
}

.login {
    height: 100vh;
}

.box{
    width: 300px;
    padding: 40px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: #191919;
    text-align: center;
    box-shadow: 3px 3px 10px #2c2c2c;
}
.box h1{
    color: white;
    text-transform: uppercase;
    font-weight: 700;
}
.box input[type = "text"],.box input[type = "password"]{
    border: 0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 2px solid #3498db;
    padding: 14px 10px;
    width: 200px;
    outline: none;
    color: white;
    border-radius: 24px;
    transition: 0.25s;
}
.box input[type = "text"]:focus,.box input[type = "password"]:focus{
    width: 280px;
    border-color: #2ecc71;
}
.box button {
    border: 0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 2px solid #2ecc71;;
    padding: 14px 40px;
    outline: none;
    color: white;
    border-radius: 24px;
    transition: 0.25s;
    cursor: pointer;
}
.box button:hover {
   background: #2ecc71;
}
/* Root Variables */

:root {
    --color-primary: #7380ec;
    --color-danger: #ff7782;
    --color-success: #41f1b6;
    --color-warning: #ffbb55;
    --color-white: #fff;
    --color-info-dark: #7d8da1;
    --color-info-light: #dce1eb;
    --color-dark: #363949;
    --color-light: rgba(132, 139, 200, 0.18);
    --color-primary-variant: #111e88;
    --color-dark-variant: #677483;
    --color-background: #f6f6f9;

    --card-border-radius: 2rem;
    --border-radius-1: 0.4rem;
    --border-radius-2: 0.8rem;
    --border-radius-3: 1.2rem;

    --card-padding: 1.8rem;
    --padding-1: 1.2rem;

    --box-shadow: 0 2rem 3rem var(--color-light);

    --display-menu: none;
}

* {
    margin: 0;
    padding: 0;
    outline: 0;
    -webkit-appearance: none;
            appearance: none;
    border: 0;
    text-decoration: none;
    list-style: none;
    /* box-sizing: border-box; */
}

html {
    font-size: 14px;
}

body {

    width: 100vw;
    height: 100vh;
    font-family: poppins, sans-serif;
    font-size: 0.88rem;
    background: #f6f6f9;
    background: var(--color-background);
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    overflow-x: hidden;
    color: #363949;
    color: var(--color-dark);
}

.container {
    display: grid;
    width: 96%;
    margin: 0 auto;
    grid-gap: 1.8rem;
    gap: 1.8rem;
    grid-template-columns: 14rem auto 23rem;
}

a {
    color: #363949;
    color: var(--color-dark);
    width: 100%;
}

img {
    display: block;
    width: 100%;
}

h1 {
    font-weight: 800;
    font-size: 1.8rem;
}

h2 {
    font-size: 1.4rem;
}

h3 {
    font-size: 0.87rem;
}

h4 {
    font-size: 0.8rem;
}

h5 {
    font-size: 0.77rem;
}

small {
    font-size: 0.75rem;
}

.profile-photo {
    width: 2.8rem;
    height: 2.8rem;
    border-radius: 50%;
    overflow: hidden;
}

.text-muted {
    color: #7d8da1;
    color: var(--color-info-dark);
}

p {
    color: #677483;
    color: var(--color-dark-variant);
}

b {
    color: #363949;
    color: var(--color-dark);
}

.primary {
    color: #7380ec;
    color: var(--color-primary);
}

.danger {
    color: #ff7782;
    color: var(--color-danger);
}

.success {
    color: #41f1b6;
    color: var(--color-success);
}

.warning {
    color: #ffbb55;
    color: var(--color-warning);
}

aside {
    height: 100vh;
}

aside .top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.4rem;
}

aside .logo {
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
}

aside .top img {
    width: 2rem;
    height: 2rem;
}

aside .close {
    display: none;
}

aside .sidebar {

    display: flex;
    flex-direction: column;
    height: 86vh;
    position: relative;
    top: 3rem;
}

aside h3 {
    font-weight: 800;
}

aside .material-icons-sharp {
    size: 200px;
}

aside .sidebar a {
    display: flex;
    color: #7d8da1;
    color: var(--color-info-dark);
    margin-left: 2rem;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: center;
    position: relative;
    height: 3.7rem;
    transition: all 300ms ease;
}

aside .sidebar a span{
    font-size: 1.2rem;
    transition: all 300ms ease;
}

aside .sidebar a:last-child {
    position: absolute;
    bottom: 2rem;
    width: 100%;
}

aside .sidebar a.active {
    background: rgba(132, 139, 200, 0.18);
    background: var(--color-light);
    color: #7380ec;
    color: var(--color-primary);
    margin-left: 0;
} 

aside .sidebar a.active:before {
    content: "";
    width: 6px;
    height: 100%;
    background: #7380ec;
    background: var(--color-primary);
}

aside .sidebar a.active span {
    color: #7380ec;
    color: var(--color-primary);
    margin-left: calc(1rem - 3px);
}

aside .sidebar a:hover {
    color: #7380ec;
    color: var(--color-primary);
    
}

aside .sidebar a:hover span {
    margin-left: 1rem;
}

aside .sidebar .message-count {
    background: #ff7782;
    background: var(--color-danger);
    color: #fff;
    color: var(--color-white);
    padding: 2px 10px;
    font-size: 11px;
    border-radius: 0.4rem;
    border-radius: var(--border-radius-1);
}

main {
    margin-top: 1.4rem;
}

main .date{
    display: inline-block;
    background: rgba(132, 139, 200, 0.18);
    background: var(--color-light);
    border-radius: 0.4rem;
    border-radius: var(--border-radius-1);
    margin-top: 1rem;
    padding: 0.5rem 1.6rem;
}

main .date input[type="date"] {
    background: transparent;
    color: #363949;
    color: var(--color-dark);
}

main .insights {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.6rem;
    gap: 1.6rem;
}

main .insights > div {
    background: #fff;
    background: var(--color-white);
    right: 40px;
    padding: 1.8rem;
    padding: var(--card-padding);
    border-radius: 2rem;
    border-radius: var(--card-border-radius);
    margin-top: 1rem;
    box-shadow: 0 2rem 3rem rgba(132, 139, 200, 0.18);
    box-shadow: var(--box-shadow);
    transition: all 300ms ease;
}

main .insights > div:hover {
    box-shadow: none;
}

main .insights > div span {
    background: #7380ec;
    background: var(--color-primary);
    padding: 0.2rem;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    border-radius: 50%;
    color: #fff;
    color: var(--color-white);
    font-size: 1.5rem;
}

main .insights > div.storage span {
    background: #ff7782;
    background: var(--color-danger);
}

main .insights > div.server span {
    background: #41f1b6;
    background: var(--color-success);
}

main .insights > div .middle {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
 main .insights h3 {
     margin: 1rem 0 0.6rem;
     font-size: 1rem;
 }

 main .insights .progress {
     position: relative;
     width: 92px;
     height: 92px;
     border-radius: 50%;
 }

 main .insights .progress svg {
     width: 7rem;
     height: 7rem;
 }

 main .insights svg circle {
     fill: none;
     stroke: #7380ec;
     stroke: var(--color-primary);
     stroke-width: 14;
     stroke-linecap: round;
     -webkit-transform: translate(5px, 5px);
             transform: translate(5px, 5px);
     stroke-dasharray: 110;
     stroke-dashoffset: 92;
 }

 main .insights .education svg circle {
     stroke-dasharray: 200;
     stroke-dashoffset: -30;
 }

 main .insights .storage svg circle {
    stroke-dasharray: 80;
    stroke-dashoffset: 20;
}

main .insights .server svg circle {
    stroke-dasharray: 110;
    stroke-dashoffset: 35;
}

main .insights .progress .number {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

main .insights small {
    margin-top: 1.6rem;
    display: block;
}

main .presentations {
    width: 100%;
    margin-top: 2rem;
}

main .presentations h2 {
    margin-bottom: 0.8rem;
}

main .presentations table {
    background:  #fff;
    background:  var(--color-white);
    width: 95%;
    border-radius: 2rem;
    border-radius: var(--card-border-radius);
    padding: 1.8rem;
    padding: var(--card-padding);
    text-align: center;
    box-shadow: 0 2rem 3rem rgba(132, 139, 200, 0.18);
    box-shadow: var(--box-shadow);
    transition: all 300ms ease;
}

main .presentations table:hover {
    box-shadow: none;
}

main table tbody td {
    height: 2.8rem;
    border-bottom: 1px solid rgba(132, 139, 200, 0.18);
    border-bottom: 1px solid var(--color-light);
    color: #677483;
    color: var(--color-dark-variant);
}

main table tbody tr:last-child td {
    border: none;
}

main .presentations a {
    text-align: center;
    display: block;
    margin: 1rem auto;
    color: #7380ec;
    color: var(--color-primary);
}

main .presentations .showButton:hover {
    color: #677483;
    color: var(--color-dark-variant);
}

.right {
    margin-top: 1.4rem;
}

.right .top {
    display: flex;
    justify-content: end;
    grid-gap: 2rem;
    gap: 2rem;
}

.right .top button {
    display: none;
}

.right .theme-toggler {
    background: rgba(132, 139, 200, 0.18);
    background: var(--color-light);
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 1.6rem;
    width: 4.2rem;
    cursor: pointer;
    border-radius: 0.4rem;
    border-radius: var(--border-radius-1);
}

.right .theme-toggler span {
    font-size: 1.2rem;
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.right .theme-toggler .material-icons-shaarp {
    font-size: 0.9rem;
}

.right .theme-toggler span.active {
    background-color: #7380ec;
    background-color: var(--color-primary);
    color: white;
    border-radius: 0.4rem;
    border-radius: var(--border-radius-1);
}

.right .top .profile {
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    text-align: right;
}

.right .planner {
    margin-top: 1rem;
}

.right .planner h2 {
    margin-bottom: 0.8rem;
}

.right .planner .updates {
    background: #fff;
    background: var(--color-white);
    padding: 1.8rem;
    padding: var(--card-padding);
    border-radius: 2rem;
    border-radius: var(--card-border-radius);
    box-shadow: 0 2rem 3rem rgba(132, 139, 200, 0.18);
    box-shadow: var(--box-shadow);
    transition: all 300ms ease;
}

.right .planner .updates:hover {
    box-shadow: none;
}

.right .planner .updates .update {
    border-bottom: 1px solid rgba(132, 139, 200, 0.18);
    border-bottom: 1px solid var(--color-light);
    padding-bottom: 5px;
    padding-top: 5px;
}

.right .planner .updates .update:last-child {
    border-bottom: none;
    padding-bottom: 5px;
    padding-top: 5px;
}

.right .homework {
    margin-top: 2rem;
}

.right .homework h2 {
    margin-bottom:  0.8rem;
}

.right .homework .item {
    background: #fff;
    background: var(--color-white);
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 0.7rem;
    padding: 1rem 1.8rem;
    padding: 1rem var(--card-padding);
    border-radius: 1.2rem;
    border-radius: var(--border-radius-3);
    box-shadow: 0 2rem 3rem rgba(132, 139, 200, 0.18);
    box-shadow: var(--box-shadow);
    transition: all 300ms ease;
}

.right .homework .item:hover {
    box-shadow: none;
}

.right .homework .item .right {
    display: flex;
    justify-content: space-between;
    align-items: start;
    margin: 0;
    width: 100%;
}

.right .homework .item .icon {

    padding: 0.2rem;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 8px;
    font-size: 1.5rem;
    color: #fff;
    color: var(--color-white);
    border-radius: 50%;
    background: #7380ec;
    background: var(--color-primary);
    display: flex;
}

.right .homework .add-task {
    background-color: transparent;
    border: 2px dashed #7380ec;
    border: 2px dashed var(--color-primary);
    color: #7380ec;
    color: var(--color-primary);
    display: flex;
    align-items: center;
    justify-content: center;
}

.right .homework .add-task div {
    display: flex;
    align-items: center;
    grid-gap: 0.6rem;
    gap: 0.6rem;
}
.right .homework .add-task .plus {
    color: #7380ec;
    color: var(--color-primary);
    margin-top: 6px;
    font-size: 1.5rem;
}

.right .homework .add-task div h3 {
    font-weight: 600;
}

@media screen and (max-width: 1200px) {
    .container {
        width: 94%;
        grid-template-columns: 7rem auto 23rem;
    }

    aside .logo h2 {
        display: none;
    }

    aside .sidebar h3 {
        display: none;
    }

    aside .sidebar a {
        width: 5.6rem;
    }

    aside .sidebar a:last-child {
        position: relative;
        margin-top: 1.8rem;
    }

    main .insights {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }

    main .presentations {
        position: absolute;
        margin: 3rem 0 0 0 ;
        width: 75%;    
    }

    main .presentations .Status {
        display: none;
    }

    main .presentations table {
        width: 100%;
    }

}

@media screen and (max-width: 768px) {
    .container {
        width: 100%;
        grid-template-columns: 1fr;
    }

    aside {
        position: fixed;
        left: -100%;
        background: #fff;
        background: var(--color-white);
        width: 18rem;
        z-index: 3;
        box-shadow: 1rem 3rem 4rem rgba(132, 139, 200, 0.18);
        box-shadow: 1rem 3rem 4rem var(--color-light);
        height: 100vh;
        padding-right: 1.8rem;
        padding-right: var(--card-padding);
        display: none;
        display: var(--display-menu);
        -webkit-animation: showMenu 400ms ease forwards;
                animation: showMenu 400ms ease forwards;
    }

    @-webkit-keyframes showMenu {
        to {
            left: 0;
        }
    }

    @keyframes showMenu {
        to {
            left: 0;
        }
    }

    aside .logo {
        margin-left: 1rem;
    }

    aside .logo h2 {
        display: inline;
    }

    aside .sidebar h3 {
        display: inline;
    }

    aside .sidebar a {
        width: 100%;
        height: 3.4rem;
    }

    aside .sidebar a:last-child {
        position: absolute;
        bottom: 5rem;
    }

    aside .close {
        display: inline-block;
        cursor: pointer;
    }

    main {
        margin-top: 8rem;
        padding: 0 1rem;
    }

    main .presentations {
        position: relative;
        margin: 3rem 0 0 0 ;
        width: 87%;    
    }

    main .presentations .Status {
        display: none;
    }

    main .presentations table {
        width: 100%;
        margin: 0;
    }

    .right {
        width: 94%;
        margin: 0 auto 4rem;
    }

    .right .top {
        position: fixed;
        top: 0;
        left: 0;
        align-items: center;
        padding: 0 0.8rem;
        height: 4.6rem;
        background: #fff;
        background: var(--color-white);
        width: 100%;
        margin: 0;
        z-index: 2;
        box-shadow: 0 1rem 1rem rgba(132, 139, 200, 0.18);
        box-shadow: 0 1rem 1rem var(--color-light);
    }

    .right .top .theme-toggler {
        width: 4.4rem;
        position: absolute;
        left: 66%;
    }

    .right .profile .info {
        display: none;
    }

    .right .profile .profile-photo {
        position: fixed;
        right: 20px;
        top: 13px;
    }
    
    .right .top button {
        font-size: 2rem;
        display: inline-block;
        background: transparent;
        cursor: pointer;
        color: #363949;
        color: var(--color-dark);
        position: absolute;
        left: 1rem;
    }

    .right .top button span {
        font-size: 2rem;
    }
}
:root {
  --display-welcome: block;
  --quiz-display: none;
  --quiz-start-display: block;
}

.presentation {
  text-align: center;
}

.presentation .WelcomeSlide .images {
  position: absolute;
  width: 100%;
  height: 100vh;

}

.presentation .WelcomeSlide .images img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.presentation .WelcomeSlide .images .welcomesky {
  z-index: 1;
}

.presentation .WelcomeSlide .images .welcomestars {
  z-index: 2;
  mix-blend-mode: screen;
  -webkit-animation-name: stars;
          animation-name: stars;
  -webkit-animation-duration: 900s;
          animation-duration: 900s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  display: block;
  display: var(--display-welcome);
}

@-webkit-keyframes stars {
  0%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  50%{-webkit-transform: rotate(360deg);transform: rotate(360deg);}
  100%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}

@keyframes stars {
  0%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  50%{-webkit-transform: rotate(360deg);transform: rotate(360deg);}
  100%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}

.presentation .WelcomeSlide .images .welcomecity {
  margin-top: 3%;
  z-index: 4;
} 

.presentation .WelcomeSlide .images .welcomeback {
  z-index: 5;
}

.presentation .WelcomeSlide .images .welcomemoon {
  z-index: 4;
  display: block;
  display: var(--display-welcome);
}

.presentation .WelcomeSlide .images .welcomeman {
  z-index: 6;
}

.presentation .WelcomeSlide .images .welcomelight {
  opacity: 30%;
