  padding: 100px;
}

.presentation .WelcomeSlide .text {
  color: white;
  width: auto;
  align-items: center;
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 3;
}

.presentation .WelcomeSlide .text h1 {
  font-size: 4rem;
  -webkit-transform: translateY(270px);
          transform: translateY(270px);
