.presentation .Gliederung {
  background-color: #fff8e8;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.presentation .Gliederung #qr {
  padding-bottom: 10%;
}

.presentation .Gliederung .Title {
