/* Root Variables */

:root {
    --color-primary: #7380ec;
    --color-danger: #ff7782;
    --color-success: #41f1b6;
    --color-warning: #ffbb55;
    --color-white: #fff;
    --color-info-dark: #7d8da1;
    --color-info-light: #dce1eb;
    --color-dark: #363949;
    --color-light: rgba(132, 139, 200, 0.18);
    --color-primary-variant: #111e88;
    --color-dark-variant: #677483;
    --color-background: #f6f6f9;

    --card-border-radius: 2rem;
    --border-radius-1: 0.4rem;
    --border-radius-2: 0.8rem;
    --border-radius-3: 1.2rem;

    --card-padding: 1.8rem;
    --padding-1: 1.2rem;

    --box-shadow: 0 2rem 3rem var(--color-light);

    --display-menu: none;
}

* {
    margin: 0;
    padding: 0;
    outline: 0;
    -webkit-appearance: none;
            appearance: none;
    border: 0;
    text-decoration: none;
    list-style: none;
    /* box-sizing: border-box; */
}

html {
    font-size: 14px;
}

body {

    width: 100vw;
    height: 100vh;
    font-family: poppins, sans-serif;
    font-size: 0.88rem;
    background: #f6f6f9;
    background: var(--color-background);
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    overflow-x: hidden;
    color: #363949;
    color: var(--color-dark);
}

.container {
    display: grid;
    width: 96%;
    margin: 0 auto;
    grid-gap: 1.8rem;
    gap: 1.8rem;

}

a {
    color: #363949;
    color: var(--color-dark);
    width: 100%;
}

img {
    display: block;
    width: 100%;
}

h1 {
    font-weight: 800;
    font-size: 1.8rem;
}

h2 {
    font-size: 1.4rem;
}

h3 {
    font-size: 0.87rem;
}

h4 {
    font-size: 0.8rem;
}

h5 {
    font-size: 0.77rem;
}

small {
    font-size: 0.75rem;
}

.profile-photo {
    width: 2.8rem;
    height: 2.8rem;
    border-radius: 50%;
    overflow: hidden;
}

.text-muted {
    color: #7d8da1;
    color: var(--color-info-dark);
}

p {
    color: #677483;
    color: var(--color-dark-variant);
}

b {
    color: #363949;
    color: var(--color-dark);
}

.primary {
    color: #7380ec;
    color: var(--color-primary);
}

.danger {
    color: #ff7782;
    color: var(--color-danger);
}

.success {
    color: #41f1b6;
    color: var(--color-success);
}

.warning {
    color: #ffbb55;
    color: var(--color-warning);
}

aside {
    height: 100vh;
}

aside .top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.4rem;
}

aside .logo {
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
}

aside .top img {
    width: 2rem;
    height: 2rem;
}

aside .close {
    display: none;
}

aside .sidebar {

    display: flex;
    flex-direction: column;
    height: 86vh;
    position: relative;
    top: 3rem;
}

aside h3 {
    font-weight: 800;
}

aside .material-icons-sharp {
    size: 200px;
}

aside .sidebar a {
    display: flex;
    color: #7d8da1;
    color: var(--color-info-dark);
    margin-left: 2rem;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: center;
    position: relative;
    height: 3.7rem;
    transition: all 300ms ease;
}

aside .sidebar a span{
    font-size: 1.2rem;
    transition: all 300ms ease;
}

aside .sidebar a:last-child {
    position: absolute;
    bottom: 2rem;
    width: 100%;
}

aside .sidebar a.active {
    background: rgba(132, 139, 200, 0.18);
    background: var(--color-light);
    color: #7380ec;
    color: var(--color-primary);
    margin-left: 0;
} 

aside .sidebar a.active:before {
    content: "";
    width: 6px;
    height: 100%;
    background: #7380ec;
    background: var(--color-primary);
}

aside .sidebar a.active span {
    color: #7380ec;
    color: var(--color-primary);
    margin-left: calc(1rem - 3px);
}

aside .sidebar a:hover {
    color: #7380ec;
    color: var(--color-primary);
    
}

aside .sidebar a:hover span {
    margin-left: 1rem;
}

aside .sidebar .message-count {
    background: #ff7782;
    background: var(--color-danger);
    color: #fff;
    color: var(--color-white);
    padding: 2px 10px;
    font-size: 11px;
    border-radius: 0.4rem;
    border-radius: var(--border-radius-1);
}

main {
    margin-top: 1.4rem;
}

main .presentationsT {
    width: 130%;
    margin-top: 2rem;
}

main .presentationsT h2 {
    margin-bottom: 0.8rem;
}

main .presentationsT table {
    background:  #fff;
    background:  var(--color-white);
    width: 95%;
    border-radius: 2rem;
    border-radius: var(--card-border-radius);
    padding: 1.8rem;
    padding: var(--card-padding);
    text-align: center;
    box-shadow: 0 2rem 3rem rgba(132, 139, 200, 0.18);
    box-shadow: var(--box-shadow);
    transition: all 300ms ease;
}

main .presentationsT table:hover {
    box-shadow: none;
}
main .presentationsT .showButton:hover {
    color: #677483;
    color: var(--color-dark-variant);
}

main table tbody td {
    height: 2.8rem;
    border-bottom: 1px solid rgba(132, 139, 200, 0.18);
    border-bottom: 1px solid var(--color-light);
    color: #677483;
    color: var(--color-dark-variant);
}

main table tbody tr:last-child td {
    border: none;
}

main .presentationsT a {
    text-align: center;
    display: block;
    margin: 1rem auto;
    color: #7380ec;
    color: var(--color-primary);
}

main .presentationsT .showButton:hover {
    color: #677483;
    color: var(--color-dark-variant);
}

@media screen and (max-width: 1200px) {
    .container {
        width: 94%;
        grid-template-columns: 7rem auto 23rem;
    }

    aside .logo h2 {
        display: none;
    }

    aside .sidebar h3 {
        display: none;
    }

    aside .sidebar a {
        width: 5.6rem;
    }

    aside .sidebar a:last-child {
        position: relative;
        margin-top: 1.8rem;
    }

    main .insights {
