@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap');

:root {
  --display-welcome: block;
  --quiz-display: none;
  --quiz-start-display: block;
}

.presentation {
  text-align: center;
}

.presentation .WelcomeSlide .images {
  position: absolute;
  width: 100%;
  height: 100vh;

}

.presentation .WelcomeSlide .images img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.presentation .WelcomeSlide .images .welcomesky {
  z-index: 1;
}

.presentation .WelcomeSlide .images .welcomestars {
  z-index: 2;
  mix-blend-mode: screen;
  animation-name: stars;
  animation-duration: 900s;
  animation-timing-function: linear;
  display: var(--display-welcome);
}

@keyframes stars {
  0%{transform: rotate(0deg);}
  50%{transform: rotate(360deg);}
  100%{transform: rotate(0deg);}
}

.presentation .WelcomeSlide .images .welcomecity {
  margin-top: 3%;
  z-index: 4;
} 

.presentation .WelcomeSlide .images .welcomeback {
  z-index: 5;
}

.presentation .WelcomeSlide .images .welcomemoon {
  z-index: 4;
  display: var(--display-welcome);
}

.presentation .WelcomeSlide .images .welcomeman {
  z-index: 6;
}

.presentation .WelcomeSlide .images .welcomelight {
  opacity: 30%;
  mix-blend-mode: overlay;
  z-index: 7;
}

.presentation .WelcomeSlide .images .welcomebushes {
  z-index: 8;
}

.presentation .WelcomeSlide .images ul li {
  z-index: 121;
  color: red;
  padding: 100px;
}

.presentation .WelcomeSlide .text {
  color: white;
  width: auto;
  align-items: center;
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 3;
}

.presentation .WelcomeSlide .text h1 {
  font-size: 4rem;
  transform: translateY(270px);
}

.presentation .WelcomeSlide .text h3 {
  font-size: 1.5rem;
  transform: translateY(280px);
}

.presentation .Gliederung {
  background-color: #fff8e8;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.presentation .Gliederung #qr {
  padding-bottom: 10%;
}

.presentation .Gliederung .Title {
  position: relative;
  padding: 230px;
  font-size: 100px;
  text-decoration-line: underline;
}

.presentation .Gliederung video {
  z-index: 10;
}

.presentation .Gliederung .quizAuthor .ImageBack {
  position: relative;
}

.presentation .Gliederung .quizAuthor .content {
  text-align: center;
  margin-top: 5%;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  z-index: 1;
  position: absolute;
}

.presentation .Gliederung .quizAuthor .content h1 {
  font-size: 100px;
  font-family: Abril Fatface;
  color: #af6c41;
  display: table;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  padding: 20px;
  padding-left: 5%;
  padding-right: 5%;
  border-radius: 20px;
  background: #f1d1ab;
  box-shadow: 0 1rem 2rem rgba(25, 25, 29, 0.18);
}

.presentation .Gliederung .quizAuthor .content #questionNumber {
  margin-top: 15px;
  display: var(--quiz-display);
  font-size: 30px;
  font-weight: 500;
}

.presentation .Gliederung .quizAuthor .content #StartQuiz {
  display: var(--quiz-start-display);
  z-index: 1;
  color: #af6c41;
  background-color: white ;
  display: var(--margin-transition-view);
  padding: 20px;
  border-radius: 20px;
  box-shadow: none;
  transition: all 300ms ease;
}

.presentation .Gliederung .quizAuthor .content #StartQuiz:hover {
  z-index: 1;
  box-shadow: 0 1rem 2rem rgba(25, 25, 29, 0.18);
  background-color: #fff8e8;
}

.presentation .Gliederung .quizAuthor .content #Question {
  margin-top: 30px;
  font-size: 20px;
}

.presentation .Gliederung .quizAuthor .content #Timer {
  margin-top: 25px;
  font-size: 30px;
}

.presentation .Gliederung .quizAuthor .content #Users {
  margin-top: 25px;
  font-size: 30px;
}

.presentation .Gliederung .quizAuthor .content #qr {
  margin-top: 20px;
  padding-bottom: 0px;
  border-radius: 20px;
  display: var(--quiz-start-display);
  width: 15%;
  height: 15%;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 1rem 2rem rgba(25, 25, 29, 0.18);
}

.presentation .Gliederung .quizAuthor .content #awnsers {
  text-align: center;
  column-count: 2;
  column-gap: 10px;
  row-gap: 20px;
  width: 50vw;
  margin-left: auto;
  margin-right: auto;
  font-size: 20px;
}

.presentation .Gliederung .quizAuthor .content  #awnser1{
  padding: 10px;
  border-radius: 10px;
  background-color: #f19393;
}
.presentation .Gliederung .quizAuthor .content #awnser2{
  padding: 10px;
  border-radius: 10px;
  background-color: #b1f193;
  margin-top: 10px;
}
.presentation .Gliederung .quizAuthor .content #awnser3{
  padding: 10px;
  border-radius: 10px;
  background-color: #93def1;
}
.presentation .Gliederung .quizAuthor .content #awnser4{
  padding: 10px;
  border-radius: 10px;
  background-color: #ee93f1;
  margin-top: 10px;
}

.presentation .Gliederung .quizAuthor .content button {
  display: var(--quiz-display);
  padding: 10px;
  border-radius: 10px;
  margin-top: 20px;
  background-color: #f1d1ab;
  transition: all 200ms ease;
  font-weight: 700;
}

.presentation .Gliederung .quizAuthor .content button:hover {
  background-color: #fff8e8;
  padding: 20px;
  font-size: 15px;
}


.presentation .Gliederung .quizAuthorResults .nextpage {
  position: relative;
}

.presentation .Gliederung .quizAuthorResults .content {
  text-align: center;
  margin-top: 5%;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  z-index: 1;
  position: absolute;
}

.presentation .Gliederung .quizAuthorResults .content h1 {
  font-size: 100px;
  font-family: Abril Fatface;
  color: #af6c41;
  display: table;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  padding: 20px;
  padding-left: 5%;
  padding-right: 5%;
  border-radius: 20px;
  background: #f1d1ab;
  box-shadow: 0 1rem 2rem rgba(25, 25, 29, 0.18);
}

.presentation .Gliederung .quizAuthorResults .content #Question {
  margin-top: 30px;
  font-size: 20px;
}

.presentation .Gliederung .quizAuthorResults .content #Results {
  margin-top: 30px;
  font-size: 20px;
}

.presentation .Gliederung .quizAuthorResults .content #BarChart1 {
  position: absolute;
  margin-left: 14vw;
}

.presentation .Gliederung .quizAuthorResults .content .results {
  position: relative;
}

.presentation .Gliederung .quizAuthorResults .content .results img {
  position: absolute;
  width: 20%;
  margin-top: 4vh;
  padding: 10px;
  background: #f1d1ab;
  border-radius: 20px;
  box-shadow: 0 1rem 2rem rgba(25, 25, 29, 0.18);
  margin-left: 8vw;
}

.fadeOut{
  opacity:0;
  transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
}
.fadeIn{
  opacity:1;
  transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
}

.presentation .Gliederung .quizAuthor .content #awnsers2 {
  text-align: center;
  column-count: 2;
  column-gap: 10px;
  row-gap: 20px;
  margin-top: 30px;
  width: 50vw;
  margin-left: auto;
  margin-right: auto;
  font-size: 20px;
}

#WinnerTag {
  margin-top: 4vh;
  font-size: 4rem;
}



@media screen and (max-width: 1000px) {
  .presentation {
    display: none;
  }
  
}